.Contents {
    text-decoration: none;
    color: inherit;
    transition: all .3s;
    padding: 40px 20px;
    box-shadow: 3px 3px 13px rgb(207, 207, 207);
    border-radius: 2px;
    max-width: 900px;
    margin: 40px auto;
    display: flex;
}

.Contents:hover {
    box-shadow: 3px 3px 13px rgb(163, 163, 163);
}

.Logo {
    padding: 0 20px;
}

.Logo img {
    width: 100px;
}

.Text {
    padding: 0 20px ;
}

.Address {
    font-weight: bolder;
    font-size: 22px;
    margin-bottom: 10px;
}

@media only screen and (max-width: 910px){
    .Contents {
        margin: 40px 10px;
    }
}

@media only screen and (max-width: 650px){
    .Logo {
        padding: 0;
    }
    .Logo img {
        width: 30px;
    }

    .Address {
        font-size: 16px;
    }

    .Message {
        font-size: 14px;
    }
    

}